import React, { useState } from "react"
import { navigate } from "gatsby"
import { handleSignup } from "../../database/functions"

export default function Signup() {
  const [email, setEmail] = useState("")
  const [password, setPassword] = useState("")
  const [message, setMessage] = useState("")

  const onSuccess = () => {
    setMessage("✅ Check your email for a verification link!")
    setTimeout(() => navigate("/members/dashboard"), 2000)
  }

  const onFail = (err?: string) => {
    setMessage("❌ " + (err || "Signup failed. Please try again."))
  }

  return (
    <div style={{ maxWidth: 400, margin: "auto", padding: 20, display: "flex", flexDirection: "column", gap: 10 }}>
      <h2 style={{ textAlign: "center" }}>Create Account</h2>

      <form
        onSubmit={(e) => {
          e.preventDefault()
          handleSignup({ email, password, onSuccess, onFail })
        }}
        style={{ display: "flex", flexDirection: "column", gap: 10 }}
      >
        <input
          type="email"
          placeholder="Email"
          value={email}
          onChange={(e) => setEmail(e.target.value)}
          required
          style={{ padding: 10, border: "1px solid #ccc", borderRadius: 5 }}
        />

        <input
          type="password"
          placeholder="Password"
          value={password}
          onChange={(e) => setPassword(e.target.value)}
          required
          style={{ padding: 10, border: "1px solid #ccc", borderRadius: 5 }}
        />

        <button
          type="submit"
          style={{
            padding: 10,
            borderRadius: 5,
            background: "#2E7D32",
            color: "#fff",
            border: "none",
            cursor: "pointer",
          }}
        >
          Sign Up
        </button>
      </form>

      {message && <p style={{ textAlign: "center", marginTop: 10 }}>{message}</p>}
    </div>
  )
}
